import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import Home from '../views/Home.vue'
import GraveAnmodning from '../views/GraveAnmodning.vue'
import Email from '@/views/Email.vue'
import EmailList from '@/views/EmailList.vue'
import Workers from '@/views/Workers.vue'
import Properties from '@/views/Properties.vue'
import AnmodningList from '@/views/AnmodningList.vue'
import Playground from '@/views/Playground.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/workers',
    name: 'Workers',
    component: Workers
  },
  {
    path: '/properties',
    name: 'Properties',
    component: Properties
  },
  {
    path: '/anmodninger',
    name: 'AnmodningList',
    component: AnmodningList
  },
  // {
  //   path: '/anmodninger/:status',
  //   name: 'AnmodningList-Status',
  //   component: AnmodningList
  // },
  {
    path: '/anmodning/:id',
    name: 'GraveAnmodning',
    component: GraveAnmodning
  },
  {
    path: '/mails',
    name: 'EmailList',
    component: EmailList
  },
  {
    path: '/mails/:id',
    name: 'Mail',
    component: Email
  },
  {
    path: '/playground',
    name: 'Playground',
    component: Playground
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
