<template>
  <b-container id="breadcrumbs-container">

    <nav class="breadcrumb">
      <a class="breadcrumb-item"
         v-for="crumb in crumbs"
         :key="crumb.label"
         v-on:click="goTo(crumb.to)"
         v-bind:class="{ active: crumb.isActive }"
      >
        {{ crumb.label }}
      </a>
      <!--      <span class="breadcrumb-item active">Wow</span>-->
    </nav>

  </b-container>
</template>

<script>
import { Component, Vue, Watch } from 'vue-property-decorator'

@Component({
  components: {}
})
export default class Breadcrumbs extends Vue {
  crumbs = []

  @Watch('$route', {
    immediate: true,
    deep: true
  })
  onUrlChange (newVal) {
    this.buildCrumbs()
  }

  mounted () {
    this.buildCrumbs()
  }

  buildCrumbs () {
    this.crumbs = []
    this.crumbs.push({
      label: 'Home',
      to: 'Home',
      isActive: false
    })
    if (this.$route.name === 'AnmodningList') {
      this.crumbs.push({
        label: 'Anmodninger',
        to: 'AnmodningList',
        isActive: true
      })
    }
    if (this.$route.name === 'Workers') {
      this.crumbs.push({
        label: 'Workers',
        to: 'Workers',
        isActive: true
      })
    }
    if (this.$route.name === 'Properties') {
      this.crumbs.push({
        label: 'Properties',
        to: 'Properties',
        isActive: true
      })
    }
    if (this.$route.name === 'AnmodningList-Status') {
      this.crumbs.push({
        label: 'Anmodninger',
        to: 'AnmodningList',
        isActive: false
      })
      this.crumbs.push({
        label: this.$route.params.status,
        to: 'AnmodningList',
        isActive: true
      })
    }
    if (this.$route.name === 'GraveAnmodning') {
      this.crumbs.push({
        label: 'Anmodninger',
        to: 'AnmodningList'
      })
      this.crumbs.push({
        label: this.$route.params.id,
        isActive: true,
        to: ''
      })
    }
    if (this.$route.name === 'EmailList') {
      this.crumbs.push({
        label: 'Mails',
        to: '',
        isActive: true
      })
    }
    if (this.$route.name === 'Mail') {
      this.crumbs.push({
        label: 'Mails',
        to: 'EmailList'
      })
      this.crumbs.push({
        label: this.$route.params.id,
        isActive: true,
        to: ''
      })
    }
    // console.log('CRUMBS', this.crumbs)
  }

  goTo (gt) {
    // console.log('Breadcrumbs.gt() ', this.$route, '--', this.$route.name, gt)
    if (this.$router.name !== gt) {
      this.$router.push({ name: gt })
    }
  }
}
</script>

<style scoped>

.container {
  padding-left: 0;
}

.breadcrumb {
  margin-bottom: 0;
}

.breadcrumb-item {
  cursor: pointer;
}

.breadcrumb a {
  cursor: pointer;
}

.breadcrumb a:nth-last-child(1) {
  cursor: auto;
}

.breadcrumb a:nth-last-child(1):hover {
  color: #a4a4a6;
}

</style>
