import { Component, Vue } from 'vue-property-decorator'
import { Dictionary } from 'vue-router/types/router'
import { stateApiService } from '@/ts/restApi'

/**
 * General mixin that provides with a series of extremly nice methods...
 *
 * Route manipulation.
 * Countdown timer.
 * ...
 *
 * To include just extend your Component like so:
 *
 * export default class FooBoo extends GeneralMixin {
 *    ...
 * }
 *
 */
@Component
export class GeneralMixin extends Vue {
  barValue = 1000 // We start with a low value so we get an immediate success in order to unlock the dashboard
  countDown = 10

  // True if the backend answers
  backendAvailable = false

  addParamsToLocation (params: { [x: string]: string | number | boolean }): void {
    const merged = Object.assign({}, this.$route.query, params)
    console.log(merged)
    history.pushState(
      {},
      'LER2',
      this.$route.path +
      '?' +
      Object.keys(merged)
        .map(key => {
          return (
            encodeURIComponent(key) + '=' + encodeURIComponent(params[key])
          )
        })
        .join('&')
    )
  }

  /**
   * Removes a parameter from a router query string.
   * @param param
   */
  removeParamFromLocation (param: string): void {
    const query = this.$route.query
    delete query[param]
    history.pushState(
      {},
      'LER2',
      this.$route.path +
      '?' +
      Object.keys(query)
        .map(key => {
          return (
            encodeURIComponent(key) + '=' + encodeURIComponent(query[key] ? '' : '')
          )
        }).join('&')
    )
  }

  isEmpty (obj: Dictionary<any>) {
    return Object.keys(obj).length === 0
  }

  countDownTimer (): void {
    if (this.countDown > 0) {
      setTimeout(() => {
        this.countDown -= 1000
        this.countDownTimer()
      }, 1000)
    } else {
      this.refresh()
      this.countDown = 10000
      this.countDownTimer()
    }
  }

  async beforeMount (): Promise<void> {
    this.countDownTimer()
  }

  async refresh () {
    try {
      const response = await stateApiService.getState()
      this.backendAvailable = true
    } catch (err) {
      this.backendAvailable = false
    }
  }

  filterJSONListByKeyAndValue (data: any[], key: string, value: string | undefined) {
    const l: JSON[] = []
    data.forEach(element => {
      if (String(element.columns[key].toLowerCase()) === String(value!.toLowerCase())) {
        l.push(element)
      }
    })
    return l
  }

  /*
  Returns a random message when the bottom oldest element from the service is reached.
   */
  getRandomBottomMessage (): string {
    const msgs = [
      'Der er ikke mere at se, tid til at g hjem.',
      'Du har net bunden .... af listen.',
      'Det var det - du har set det hele.'
    ]
    return msgs[Math.floor(Math.random() * msgs.length)]
  }
}
