





















































































import { Component, Prop, Watch } from 'vue-property-decorator'
import AllStatusCardSvg from '@/components/AllStatusCardSvg.vue'
import { anmodningerApi } from '@/ts/restApi'
import { Anmodning, AnmodningStatusEnum } from '@/service'
import { IntervalMixin } from '@/mixins/interval-mixin'

@Component({
  components: { AllStatusCardSvg },
  props: [
    'title', 'btnTitle', 'filterKey', 'filterValue',
    'limit', 'imgSrc', 'to', 'notZeroImgSrc',
    'cvr', 'selectedIntrvl',
    'status']
})

export default class AnmodningerCard extends IntervalMixin {
  @Prop()
  title: string | undefined;

  @Prop()
  btnTitle: string | undefined;

  @Prop({ type: String })
  filterKey!: string;

  @Prop()
  filterValue: string | undefined;

  @Prop()
  limit: number | undefined;

  @Prop()
  imgSrc: string | undefined;

  @Prop()
  to: string | undefined;

  @Prop()
  notZeroImgSrc: string | undefined;

  @Prop()
  cvr: number | undefined;

  @Prop()
  selectedIntrvl: string | undefined;

  @Prop()
  status: AnmodningStatusEnum | undefined

  items: Anmodning[] = []
  n = 0
  error = false
  baseid = undefined
  @Watch('cvr')
  cvrChanged (newValue: number):void {
    // console.log('cvrChanged to', newValue)
    this.cvr = newValue
    this.load()
  }

  /**
   * This is cloned from the mixin as watching interval did not work
   */
  @Watch('selectedIntrvl', { immediate: true, deep: true })
  selectInterval (newVal: string):void {
    this.interval = this.calcInterval(newVal)
    this.load()
  }

  async beforeMount () {
    // TODO: get this into the Mixin
    this.interval = this.calcInterval(this.selectedInterval)
    this.load()
  }

  refresh () {
    this.load()
    // console.log('GraveForespoergelserCard refreshed')
  }

  async load () {
    /* Get total number of results = the count */
    try {
      const response = await anmodningerApi.getAnmodningCount(
        this.status,
        this.cvr,
        undefined,
        undefined,
        this.interval?.fratid,
        this.interval?.tiltid
      )
      this.n = Number(response.data?.optionalData)
      // console.log('n', this.status, ' ', this.n)
    } catch (err) {
      // console.log('AnmodningerCard Error, total anmodninger: ', err)
    }
    try {
      const response = await anmodningerApi.getAnmodninger(
        this.baseid,
        this.limit,
        this.status,
        this.cvr,
        undefined,
        undefined,
        this.interval?.fratid,
        this.interval?.tiltid)
      this.items = response.data.anmodList || []
    } catch (err) {
      // console.log('GraveForespoergelserCard Error: ', err)
      this.error = true
    }
  }

  getTitle () {
    if (this.error) {
      return 'BACKEND NOT AVAILABLE'
    } else {
      if (this.title !== 'ALLE') {
      // return this.items.length + ' ' + this.title
        return this.n + ' ANMODNINGER ' + this.title + ' I TIDSINTERVALLET'
      } else {
        return this.n + ' ANMODNINGER ' + ' I TIDSINTERVALLET'
      }
    }
  }

  /**
   * Return a different image path when number of items > 0 and notZeroImgSrc is set.
   */
  getImgSrc () {
    if (this.notZeroImgSrc && this.items.length > 0) {
      return this.notZeroImgSrc
    } else {
      return this.imgSrc
    }
  }

  goTo () {
    if (this.status) {
      console.log('beeep')
      // this.addParamsToLocation({ status: this.status.toLowerCase() })
      this.$router.push({ path: this.to, params: { status: this.status.toLowerCase() } })
    } else {
      this.$router.push({ path: this.to })
    }
  }
}
