











































import { Component, Prop, Watch } from 'vue-property-decorator'
import { mailsApi } from '@/ts/restApi'
import { Mail } from '@/service'
import { IntervalMixin } from '@/mixins/interval-mixin'
import { IIntervalFilterField } from '@/interfaces'

@Component({
  components: {},
  props: ['selectedIntrvl']
})
export default class EmailCard extends IntervalMixin {
  mails: Mail[] = []
  btnTitle = 'Alle Mails'
  error = false
  initialNewest = -1
  newest = -1
  count: number
    | undefined // Count of all mails in the system.

  // @Prop()
  interval?: IIntervalFilterField | undefined = {
    fratid: undefined,
    tiltid: undefined
  }

  @Prop()
  selectedIntrvl: string | undefined;

  /**
   * This is cloned from the mixin as watching interval did not work
   */
  @Watch('selectedIntrvl', { immediate: true, deep: true })
  selectInterval (newVal: string) {
    this.interval = this.calcInterval(newVal)
    this.load()
  }

  async beforeMount () {
    this.load()
  }

  async load () {
    try {
      const response = await mailsApi.getMailCount(
        undefined,
        this.interval?.fratid,
        this.interval?.tiltid
      )
      this.count = Number(response.data.optionalData)
    } catch (err) {
      console.log('Email Card COUNT Error', err)
    }
    try {
      const response = await mailsApi.getMails(
        undefined,
        5,
        undefined,
        this.interval?.fratid,
        this.interval?.tiltid
      )
      this.mails = response.data.mailList || []
      if (this.initialNewest === -1) {
        this.initialNewest = this.mails[0]?.id || -1
      }
    } catch (err) {
      console.log('Email Card Error', err)
      this.mails = []
      this.error = true
    }
  }

  /*
  Gui Accessor
   */
  getTitle () {
    return this.count + ' Emails'
  }

  goTo () {
    const i = 'yesterday'
    this.$router.push({ path: '/mails', query: { interval: i } })
  }
}

