

























































































import { Component, Watch } from 'vue-property-decorator'
import { anmodningerApi } from '@/ts/restApi'
import { Anmodning, AnmodningStatusEnum } from '@/service'
import { EjerMixin } from '@/mixins/ejer-mixin'
// import moment from 'moment'

@Component({
  components: {}
})
export default class AnmodningList extends EjerMixin {
  status: string | undefined
  gfnummer?: number = undefined
  anmodninger: Anmodning[] = []
  baseid = undefined
  MAXROWS = 15 // Size of the batch we load every time
  componentMounted = false // true when the component is mounted, used for taming the batch loader on roll, TODO: refactor into mixin.
  initialTopID = -1
  tid = 'ABC'
  interval = {
    fratid: undefined,
    tiltid: undefined
  }

  /**
   * Refactor into mixin
   * */
  visibilityChanged (isVisible: any, entry: any) {
    if (entry.isIntersecting && this.componentMounted) {
      // console.log('***', entry)
      this.loadNextBatch()
    }
  }

  mounted () {
    this.componentMounted = true
    this.setInitialTopID()
  }

  @Watch('$route', { immediate: true, deep: true })
  onUrlChange (newVal: any) {
    // console.log('onUrlChange', newVal)
    this.refresh()
  }

  /**
   * Returns the ID of the last element in the anmodning list on the bottom of the page.
   * We use it for lazy loading the next batch from the backend.
   *
   * Note To Bernie: Read more about Optional Chaining here:
   * https://www.typescriptlang.org/docs/handbook/release-notes/typescript-3-7.html#nullish-coalescing
   *
   */
  getBottomAnmodningID (): number {
    const mls = this.anmodninger ? this.anmodninger[this.anmodninger.length - 1] : undefined
    return mls?.id || -1
  }

  getTopAnmodningID (): number {
    const mls = this.anmodninger ? this.anmodninger[0] : undefined
    return mls?.id || -1
  }

  // TODO: Add type here
  getBatchBoundaries () {
    return {
      top: this.getTopAnmodningID(),
      bottom: this.getBottomAnmodningID()
    }
  }

  lookupEjerNavn (cvr: number):string {
    return '<TODO>'
    // return this.getEjerLabelByCVR(cvr)
  }

  /**
   *
   * Loads the next batch of anmodning at the bottom of the list.
   *
   */
  async loadNextBatch () {
    // console.log('nextBatch', this.getBottomAnmodningID(), '>', this.MAXROWS)
    const response = await anmodningerApi.getAnmodninger(
      this.getBottomAnmodningID() - 1,
      this.MAXROWS,
      this.getExtendedStatusName())
    this.anmodninger = this.anmodninger.concat(response.data.anmodList || [])
  }

  async beforeMount (): Promise<void> {
    this.gfnummer = undefined
    this.status = this.$route.query?.status
    console.log(this.status)
  }

  goTo (forespoergNr: number) {
    this.$router.push({ name: 'GraveAnmodning', params: { forespoergNr: String(forespoergNr) } })
  }

  foo () {
    console.log('foo:')
    this.refresh()
  }

  /**
   * Returns the statename, but undefined when this.status == ALLE
   * */
  getExtendedStatusName () {
    if (this.status === 'ALLE') {
      return undefined
    } else {
      return AnmodningStatusEnum[this.status as keyof typeof AnmodningStatusEnum]
    }
  }

  /*
  Returns the ID of the top element of the list on page load.
   */
  async setInitialTopID () {
    try {
      const response = await anmodningerApi.getAnmodninger(undefined, 1, this.getExtendedStatusName())
      const x = response.data.anmodList || []
      if (x.length > 0) {
        this.initialTopID = x[0]?.id || -1
      }
    } catch (err) {
      console.warn('AnmodningList error', err)
    }
  }

  async refresh () {
    try {
      const response = await anmodningerApi.getAnmodninger(this.baseid, this.MAXROWS, this.getExtendedStatusName())
      this.anmodninger = response.data.anmodList || []
    } catch (err) {
      console.warn('AnmodningList error', err)
    }
    // console.log('AnmodningerList refreshed')
  }

  getStatusser (): string[] {
    return [
      'ALLE',
      'AFSLUTTET',
      'VENTER',
      'FEJLET',
      'BLOKERET'
    ]
  }

  setDate () {
    console.log('setDate ', this.tid)
  }
}

