<template>
  <div>
    <b-container>
      <h1>Email {{ getMailId() }}</h1>
      <section id="anmodning">
        <b-row>
          <b-col md="12">
            <table class="table table-sm table-responsive">
              <thead>
              <tr>
                <!--                <th></th>-->
                <!--                <th></th>-->
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item, key) in mail" :key="key">
                <th>{{ key }}</th>
                <td style="text-align:left" v-if="searchString">
                  <text-highlight :queries="[searchString]">{{ item }}</text-highlight>
                </td>
                <td style="text-align:left" v-if="!searchString">
                  {{ item }}
                </td>
              </tr>
              </tbody>
            </table>
          </b-col>
        </b-row>
      </section>
      <section id="smap">
        <b-row>
          <b-col>
<!--            <div id="map"></div>-->
          </b-col>
        </b-row>
      </section>
    </b-container>
  </div>
</template>

<script>

import { Component, Prop, Vue } from 'vue-property-decorator'
import { mailsApi } from '@/ts/restApi'
import { Mail } from '@/service'
import Map from 'ol/Map'
import View from 'ol/View'
import { defaults as defaultControls, ScaleLine } from 'ol/control'
import { Tile as TileLayer, Vector as VectorLayer } from 'ol/layer'
import { OSM, Vector as VectorSource } from 'ol/source'

@Component({
  components: {}
})
export default class Email extends Vue {
  mail = null
  searchString = ''
  id = -1

  async beforeMount () {
    const response = await mailsApi.getMails(this.$route.params.id, 1)
    // console.log(response)
    this.mail = response.data.mailList[0]
    this.id = this.mail.id
    if ('searchString' in this.$route.query) {
      this.searchString = this.$route.query.searchString
    }
    this.initiateMap()
  }

  getMailId () {
    return this.id
  }

  initiateMap () {
    // create vector layer
    const source = new VectorSource()
    const vector = new VectorLayer({
      source: source
    })
    // create title layer
    const raster = new TileLayer({
      source: new OSM()
    })
    // create map with 2 layer
    const map = new Map({
      controls: defaultControls().extend([
        new ScaleLine({
          units: 'degrees'
        })
      ]),
      target: 'map',
      layers: [raster, vector],
      view: new View({
        projection: 'EPSG:4326',
        center: [0, 0],
        zoom: 2
      })
    })
  }
}
</script>

<style scoped>
  #map {
    position: absolute;
    margin: 0;
    padding: 0;
    height: 500px;
    width: 99%;
  }
</style>
