



























































































































import { Component, Watch } from 'vue-property-decorator'
import { Mail, MailStatusEnum } from '@/service'
import { mailsApi } from '@/ts/restApi'
import { IntervalMixin } from '@/mixins/interval-mixin'
import IntervalFilterField from '@/components/IntervalFilterField.vue'

export enum ExtendedMailTypeEnum {
  ALLE = 'ALLE',
  INFO = 'INFO',
  DATAFEJL = 'DATAFEJL',
  SYSTEMFEJL = 'SYSTEMFEJL'
}

@Component({
  components: {
    IntervalFilterField
  }
})
export default class EmailList extends IntervalMixin {
  // The number of elements to fetch
  MAXROWS = 50

  mails?: Mail[] = []
  type?: ExtendedMailTypeEnum = ExtendedMailTypeEnum.ALLE
  tekst = ''
  mntd = false // true when the component is mounted, used for taming the batch loader on scroll

  initialNewestID = -1 // TODO: get this working...

  count = 0

  statesToInclude = [
    undefined,
    MailStatusEnum.VENTER,
    MailStatusEnum.AFSENDT,
    MailStatusEnum.BLOKERET,
    MailStatusEnum.BEHANDLES
  ]

  // TODO dynamic on top of MailStatusEnum
  counts: { [index: string]: any } = {
    VENTER: 0,
    AFSENDT: 0,
    BLOKERET: 0,
    BEHANDLES: 0
  }

  async getNewest () {
    try {
      const response = await mailsApi.getMails(
        undefined,
        1,
        undefined
      )
      const m = response.data.mailList || []
      return m[0]
    } catch (err) {
      console.log(err)
    }
  }

  totalCount = 0

  status: MailStatusEnum | MailStatusEnum.AFSENDT | undefined

  statusUpdate (status: MailStatusEnum) {
    this.status = status
    this.load(0, this.MAXROWS)
  }

  getLabelWithCount (label: string): string {
    return label + ' (' + this.counts[label.toUpperCase()] + ')'
  }

  /**
   * Watches what comes out of the time field and reloads the dataset.
   */
  @Watch('interval', { immediate: true, deep: true })
  intervalChanged (newInterval: any) {
    this.load(0, this.MAXROWS)
  }

  /**
   *
   * On mount. Looks into the url param 'interval' and calculates interval.
   * Falls back to 24timer.
   *
   * */
  mounted () {
    this.mntd = true
    // console.log(String(this.$route.query.interval), this.interval)
    this.load(0, this.MAXROWS)
  }

  async getCountForStatus (status: MailStatusEnum): Promise<number> {
    const countResponse = await mailsApi.getMailCount(
      status,
      this.interval?.fratid,
      this.interval?.tiltid
    )
    return Number(countResponse.data.optionalData)
  }

  async load (offset: number, maxrows: number) {
    try {
      let s
      if (this.status !== undefined) {
        s = this.status!
      }
      this.totalCount = 0
      Object.values(MailStatusEnum).forEach((status) => {
        this.getCountForStatus(status).then(response => {
          this.counts[status] = response
          this.totalCount = this.totalCount + response
        })
      })

      const response = await mailsApi.getMails(
        undefined,
        maxrows,
        s,
        this.interval?.fratid,
        this.interval?.tiltid
      )
      this.mails = response.data.mailList || []
      if (this.initialNewestID === -1) {
        this.initialNewestID = this.mails[0]?.id || -1
      }
    } catch (err) {
      console.warn('ERR', err)
    }
  }

  /**
   * Returns the ID of the last element in the email list on the bottom of the page.
   * We use it for lazy loading the next batch from the backend.
   *
   * Note To Bernie: Read more about Optional Chaining
   * here: https://www.typescriptlang.org/docs/handbook/release-notes/typescript-3-7.html#nullish-coalescing
   *
   */
  getBottomMailID (): number {
    const mls = this.mails ? this.mails[this.mails.length - 1] : undefined
    return mls?.id || -1
  }

  getTopMailID (): number {
    const mls = this.mails ? this.mails[0] : undefined
    return mls?.id || -1
  }

  /**
   *
   * Loads the next batch of emails at the bottom of the list.
   *
   */
  async loadNextBatch () {
    console.log('loadNextBatch', this.getBottomMailID(), '>', this.MAXROWS)
    const response = await mailsApi.getMails(
      this.getBottomMailID() - 1,
      this.MAXROWS,
      this.status,
      this.interval?.fratid,
      this.interval?.tiltid
    )
    console.log('loadNextBatch', response.config.url)
    console.log('loadNextBatch ', response.data.mailList)
    // this.mails = response.data.mailList || []
    this.mails = this.mails?.concat(response.data.mailList || [])
  }

  countDownTimer () {
    if (this.countDown > 0) {
      setTimeout(() => {
        this.countDown -= 1000
        this.countDownTimer()
      }, 1000)
    } else {
      this.refresh()
    }
  }

  async loadTop (newest: number) {
    // TODO: this.status again
    const status = undefined
    // TODO: use negative baseid
    const response = await mailsApi.getMails(newest, 100, status)
    const mails: Mail[] = response.data.mailList || []
    // console.log('loading top', response.data.length, response.data[0].id || -1, response.data[response.data.length - 1].id || -1)
    const mm: Mail[] = []
    mails.forEach(element => {
      // console.log('newest', newest, element.id)
      if ((element.id || -1) > newest) {
        // console.log('pushing', element.id)
        mm.push(element)
      }
    })
    if (mm.length > 0) {
      this.mails = mm.concat(this.mails!)
      // this.mails = mm.concat(this.mails)
      // TODO: fix this
      // this.loaded.newest = mm[0].id || -1
      // const sound = new Audio(require('@/assets/ding.mp3')).play()
    }
  }

  filterByMailType (mails?: Mail[]): Mail[] {
    return mails || []
    // const m: Mail[] = []
    // if (this.type !== ExtendedMailTypeEnum.ALLE) {
    //   mails || [].forEach(element => {
    //     if (element == null) {
    //     } else {
    //       if (element.type === this.type) {
    //         m.push(element)
    //       }
    //     }
    //   })
    //   return m
    // } else {
    //   return mails || []
    // }
  }

  /**
   * Returns the ID of the last element in the email list on the bottom of the page.
   * We use it for lazy loading the next batch from the backend.
   *
   * Note To Bernie: Read more about Optional Chaining here: https://www.typescriptlang.org/docs/handbook/release-notes/typescript-3-7.html#nullish-coalescing
   *
   */
  getLastEmailID (): number {
    const mls = this.mails ? this.mails[this.mails.length - 1] : undefined
    return mls?.id || -1
  }

  filterByMailStatus (mails: Mail[]): Mail[] {
    const m: Mail[] = []
    if (this.status !== undefined) {
      mails.forEach(element => {
        if (element.status === this.status) {
          m.push(element)
        }
      })
      return m
    } else {
      return mails
    }
  }

  filterByText (mails: Mail[], text: string) {
    if (text.length > 0) {
      const l: Mail[] = []
      const searchFields = ['body', 'tilAdresser', 'statusTekst', 'subject']
      mails.forEach(element => {
        let found = false
        searchFields.forEach(field => {
          if (String(this.getValue(element, field).toLowerCase()).indexOf(String(text.toLowerCase())) > -1) {
            found = true
          }
        })
        if (found) {
          l.push(element)
        }
      })
      return l
    } else {
      return mails
    }
  }

  getMails () {
    let mm = this.filterByMailType(this.mails)
    mm = this.filterByText(mm, this.tekst)
    return this.filterByMailStatus(mm)
  }

  getTyper () {
    return ExtendedMailTypeEnum
  }

  getStatusser () {
    return MailStatusEnum
  }

  foo () {
    // console.log('foo', this.type, this.tekst)
  }

  getValue (obj: any, key: string) {
    return obj[key]
  }

  goTo (mailId: number) {
    this.$router.push({ name: 'Mail', params: { id: String(mailId) }, query: { searchString: this.tekst } })
  }

  /**
   Called when the bottom of the current batch is scrolled into the browser viewport.
   **/
  visibilityChanged (isVisible: any, entry: any) {
    if (entry.isIntersecting && this.mntd) {
      console.log('visibilityChanged')
      this.loadNextBatch()
    }
  }
}
