import { Component, Watch } from 'vue-property-decorator'
import { GeneralMixin } from '@/mixins/general-mixin'
import { IIntervalFilterField } from '@/interfaces'
import moment from 'moment'

/**
 *
 * Interval Mixin - contains everything for setting and getting the interval
 *
 *
 */
@Component
export class IntervalMixin extends GeneralMixin {
  interval?: IIntervalFilterField | undefined = {
    fratid: undefined,
    tiltid: undefined
  }

  // format for the dates sent towards the backend.
  DATE_FORMAT = 'YYYY-MM-DDTHH:MM:SSZ'

  // Format for the date picker components shown for interval.
  DATE_PICKER_FORMAT = 'YYYY-MM-DD HH:MM'

  datepickerInterval = {
    fra: new Date(String(moment())),
    til: new Date(String(moment()))
  }

  selectedInterval = '24-hours'

  mounted () {
    // console.log(String(this.$route.query.interval), ' valid ', this.isValidInterval(String(this.$route.query.interval)))
    if (this.isValidInterval(String(this.$route.query.interval))) {
      this.interval = this.calcInterval(String(this.$route.query.interval))
    } else {
      console.log('Fallback to 24-hours.')
      this.interval = this.calcInterval('24-hours')
    }
  }

  /**
   * We are watching the selectedInterval field, typically driven from some select / dropdown in the GUI.
   * @param newVal
   */
  @Watch('selectedInterval', { immediate: true, deep: true })
  selectInterval (newVal: string) {
    this.addParamsToLocation({ interval: String(newVal) })
    this.interval = this.calcInterval(newVal)
  }

  validIntervals = [
    '24-hours',
    'today',
    'yesterday',
    'this-week',
    'this-month',
    'all',
    'interval'
  ]

  /*
  Returns true if an interval name is valid
   */
  isValidInterval (intrvl: string) {
    return this.validIntervals.some(x => x === intrvl)
  }

  getIntervalLabel () {
    // console.log('gIL ', this.selectedInterval)
    switch (this.selectedInterval) {
      case '24-hours':
        return 'det seneste dgn'
      case 'today':
        return 'i dag'
      case 'yesterday':
        return 'i gr'
      case 'this-week':
        return 'i denne uge'
      case 'this-month':
        return 'i denne mned'
      case 'alle':
        return 'alle'
      case undefined:
        return '<undefined>'
    }
  }

  /**
   * Calculates a time interval given a string 'selected' {today, yesterday, this-week, this-month, all, interval}
   * @param selected
   */
  calcInterval (selected :string): IIntervalFilterField | undefined {
    switch (selected) {
      case '24-hours':
        return {
          fratid: moment().subtract(24, 'h').format(this.DATE_FORMAT),
          tiltid: moment().endOf('day').format(this.DATE_FORMAT)
        }
      case 'today':
        return {
          fratid: moment().startOf('day').format(this.DATE_FORMAT),
          tiltid: moment().endOf('day').format(this.DATE_FORMAT)
        }
      case 'yesterday':
        return {
          fratid: moment().subtract(1, 'd').startOf('day').format(this.DATE_FORMAT),
          tiltid: moment().subtract(1, 'd').endOf('day').format(this.DATE_FORMAT)
        }
      case 'this-week':
        // console.log('this-week')
        return {
          fratid: moment().startOf('week').format(this.DATE_FORMAT),
          tiltid: moment().format(this.DATE_FORMAT)
        }
      case 'this-month':
        // console.log('this-month')
        return {
          fratid: moment().startOf('month').format(this.DATE_FORMAT),
          tiltid: moment().format(this.DATE_FORMAT)
        }
      case 'alle':
        // console.log('ALLE')
        return {
          fratid: undefined,
          tiltid: undefined
        }
      case 'interval':
        return {
          fratid: moment(new Date(this.datepickerInterval.fra)).format(this.DATE_FORMAT),
          tiltid: moment(new Date(this.datepickerInterval.til)).format(this.DATE_FORMAT)
        }
      case undefined:
        return {
          fratid: undefined,
          tiltid: undefined
        }
    }
  }
}
