<template>
  <div>
  <h1>Properties</h1>
    <table class="table table-sm table-responsive">
      <thead>
      <tr>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, key) in getProperties()" :key="key">
        <th>{{ key }}</th>
        <td style="text-align:left">
          {{ item }}
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { Component, Vue } from 'vue-property-decorator'
import { configuationService } from '@/ts/restApi'
// import EventBus from '../eventBus'

@Component({
  components: {}
})
export default class Properties extends Vue {
  properties = []
  async beforeMount () {
    const response = await configuationService.getProperties()
    this.properties = response.data.properties
  }

  getProperties () {
    return this.properties
  }
}
</script>

