<template>
  <section id="abstract">
    <b-modal id="modal-1" title="INGEN FORBINDELSE" v-model="modalShow" ok-disabled cancel-disabled size="xl">
      <p class="my-4">
        <b-container>
          <b-row>
            <b-col>
              <b-img :src="require('../assets/Bang.svg')"></b-img>
            </b-col>
            <b-col>
               <h1>DESVRRE....</h1>
               <div>Det lykkedes os ikke lige at f fat i servicen. Hang on.... vi prver lige igen!</div>
            </b-col>
          </b-row>
        </b-container>
      </p>
    </b-modal>
    <b-container>
      <b-progress :value="countDown" :max="10000" animated height="2px"></b-progress>
      <b-row class="auto">
        <b-col cols=3 class="well mb-5 pl-0 ml-0" v-for="worker in getWorkersKeys()" :key="worker">
          <b-card-img :src="getImage(worker)" alt="Image" class="rounded-0 mh100" fluid thumbnail></b-card-img>
          <h3>{{worker}}</h3>
          {{getWorker(worker).state}}
          <button v-on:click="buttClick(worker, action)"
                  v-for="action in actions"
                  v-bind:key="action"
                  :disabled="buttDisabled(worker, action)"
                  class="btn btn-primary btn-block mb-2">
            {{ action }}
          </button>
<!--          <i style="font-size: 8px">{{getWorker(worker).stopCause}}</i>-->
          Last start time: {{getWorker(worker).lastStartTime | formattedDateFromInteger('YYYY-MM-DD hh:mm')}}
        </b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>

import { Component, Vue } from 'vue-property-decorator'
import { stateApiService } from '@/ts/restApi'

@Component({
  components: {}
})
export default class Workers extends Vue {
  workers = {}
  actions = ['start', 'stop']
  countDown = 10000
  modalShow = false
  err = ''
  async beforeMount () {
    this.countDown = 10000
    try {
      this.load()
    } catch (err) {
      console.log('ERR', err)
      this.modalShow = true
    }
  }

  async load () {
    this.countDown = 10000
    try {
      const response = await stateApiService.getState()
      // console.log(response)
      this.workers = response.data.stateInfo.workers
    } catch (err) {
      // console.log('CAUGHT', err)
      this.err = err
      this.modalShow = true
    }
    this.countDownTimer()
  }

  getWorkersKeys () {
    return Object.keys(this.workers)
  }

  getWorker (workerId) {
    return this.workers[workerId]
  }

  buttDisabled (worker, action) {
    if (this.workers[worker].state === 'RUNNING' && action === 'start') {
      return true
    }
    if (this.workers[worker].state === 'STOPPED' && action === 'stop') {
      return true
    }
    return false
  }

  getImage (worker) {
    if (this.workers[worker].state === 'RUNNING') {
      return require('../assets/GREEN.svg')
    } else {
      return require('../assets/Fejlet-Square.svg')
    }
  }

  async buttClick (worker, what) {
    console.log(worker, what)
    try {
      // const setManual = await stateApiService.setMode('MANUAL')
      const response = await stateApiService.startWorker(worker)
      console.log('setManual', response)
      // try {
      console.log('xxx')
      if (what === 'start') {
        const response = await stateApiService.startWorker(worker)
        console.log(response)
      } else {
        const response = await stateApiService.stopWorker(worker)
        console.log(response)
      }
      // } catch (error) {
      //   console.log('ERR', error)
      // }
    } catch (err) {
      console.warn('ERR', err)
    }
  }

  countDownTimer () {
    if (this.countDown > 0) {
      setTimeout(() => {
        this.countDown -= 1000
        this.countDownTimer()
      }, 1000)
    } else {
      this.load()
    }
  }
}

</script>

