<template>
  <svg v-bind:width="getWidth()"
       v-bind:height="getHeight()"
       v-bind:viewBox="getViewBox()"
       version="1.1"
       xmlns="http://www.w3.org/2000/svg"
       xmlns:xlink="http://www.w3.org/1999/xlink">
    <g id="Alle-Square-Dynamic" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" opacity="0.5">
      <rect id="rectangle-red" fill="#821307" x="0" y="0"
            v-bind:width="getWidth()"
            v-bind:height="getStatusHeight('FEJLET')"
            v-if="isNotInfinity('FEJLET')"
      >
      </rect>
      <rect id="rectangle-yellow"
            fill="#DEC55B"
            x="0"
            v-if="isNotInfinity('FEJLET')"
            v-bind:y="getStatusHeight('FEJLET')"
            v-bind:width="getWidth()"
            v-bind:height="getStatusHeight('VENTER')">
      </rect>
      <rect id="rectangle-green" fill="#708B55" x="0"
            v-bind:y="getStatusHeight('FEJLET') + getStatusHeight('VENTER')"
            v-bind:width="getWidth()"
            v-bind:height="getStatusHeight('AFSLUTTET')">
      </rect>
    </g>
  </svg>
</template>
<script>
import { Component, Prop, Watch } from 'vue-property-decorator'
import { IntervalMixin } from '@/mixins/interval-mixin'
import { AnmodningStatusEnum } from '@/service'
import { anmodningerApi } from '@/ts/restApi'

/**
 *
 * A component, that shows a dynamic svg, indicating the amount of forsprgelser.
 *
 */
@Component({
  components: {},
  props: ['width', 'height', 'selectedIntrvl']
})
export default class AllStatusCardSvg extends IntervalMixin {
  @Prop()
  width

  @Prop()
  height

  @Prop()
  selectedIntrvl

  sum=0

  data = {
    VENTER: 0,
    FEJLET: 50,
    AFSLUTTET: 0,
    undefined: 50
  }

  async beforeMount () {
    this.interval = this.calcInterval(this.selectedInterval)
    this.load()
  }

  @Watch('selectedIntrvl', { immediate: true, deep: true })
  selectInterval (newVal) {
    this.interval = this.calcInterval(newVal)
    this.load()
  }

  async load () {
    const statesToPull = [undefined, AnmodningStatusEnum.VENTER, AnmodningStatusEnum.FEJLET, AnmodningStatusEnum.AFSLUTTET]
    for (const status of statesToPull) {
      try {
        const response = await anmodningerApi.getAnmodningCount(
          status,
          this.cvr,
          undefined,
          undefined,
          this.interval?.fratid,
          this.interval?.tiltid
        )
        this.data[status] = await Number(response.data?.optionalData)
      } catch (err) {
        console.log('AllStatusCardSvg Error: ', err)
      }
    }
  }

  /**
   *  Returns the height for one of the segments. In pixels
   * @param status
   * @returns {number}
   */
  getStatusHeight (status) {
    return Math.trunc((this.data[status] / this.data[undefined]) * this.height)
  }

  isNotInfinity (status) {
    return this.getStatusHeight(status) !== Infinity
  }

  getViewBox () {
    return '0 0 ' + this.width + ' ' + this.height
  }

  getWidth () {
    return this.width
  }

  getHeight () {
    return this.height
  }
}
</script>

