











































import { Component, Watch } from 'vue-property-decorator'
import Datepicker from 'vuejs-datepicker'
import { IntervalMixin } from '@/mixins/interval-mixin'

/**
 * A field that provides with a series of radiobuttons and date import fields.
 * Returns a dict of strings of an interval we use for filtering the date time of emails and anmodninger.
 *
 * GMT 0
 *
 */
@Component({
  components: {
    Datepicker
  }
})
export default class IntervalFilterField extends IntervalMixin {
  foo = 'one'

  update (newVal: any) {
    this.selectedInterval = newVal
    this.$emit('input', this.calcInterval(this.selectedInterval))
  }

  updateDatepickerFrom (x: any) {
    this.datepickerInterval.fra = x
    this.$emit('input', this.calcInterval(this.selectedInterval))
  }

  updateDatepickerTo (x: any) {
    this.datepickerInterval.til = x
    this.$emit('input', this.calcInterval(this.selectedInterval))
  }

  getDisabledDatesFrom () {
    return {
      from: this.datepickerInterval.til
    }
  }

  getDisabledDatesTo () {
    return {
      to: this.datepickerInterval.fra
    }
  }

  focusDatepicker () {
    console.log('ffocusDatepicker')
  }
}

