<template>
    <b-container>
      <b-row class="toggle-row">
          <b-col class="col-auto pl-0 pr-0 my-auto" v-bind:class="{'zero': tlVisible}">
            <b-img style="float:left;"
                   class="traffic-light"
                   :src="getStateImage(state)">
            </b-img>
          </b-col>
          <b-col class="col-auto my-auto" v-bind:class="{'zero': tlVisible}">
            {{ state }}
          </b-col>
          <b-col class="my-auto text-left description" v-bind:class="{'zero': tlVisible}">
            {{getStateByColor(state).description}}
          </b-col>
          <b-col class="my-auto text-right">
            <b-icon icon="chevron-down"
                    style="cursor: pointer;"
                    :rotate="getChevronRotate()" v-b-toggle.collapse-1
                    aria-hidden="true"
                    font-scale="2">
            </b-icon>
          </b-col>
      </b-row>
        <b-collapse id="collapse-1" class="mt-2" v-model="tlVisible">
        <b-row v-for="state in states" :key="state.color">
          <b-col class="col-auto pl-0 pr-0 my-auto">
            <b-img style="float:left;"
                   class="traffic-light"
                   v-bind:class="{'disabled': isDisabled(state.color)}"
                   :src="getStateImage(state.color)">
            </b-img>
          </b-col>
          <b-col class="col-auto my-auto">
            {{ state.color }}
          </b-col>
          <b-col class="my-auto text-left description">
  <!--          <span class="d-none d-md-block">-->
              {{state.description}}
          </b-col>
          <b-col class="col-auto my-auto">
            <b-button v-on:click="setState(state.setState)" v-if="state.setStateButtonLabel">
              {{state.setStateButtonLabel}}
            </b-button>
          </b-col>
        </b-row>
      </b-collapse>
      <!--------->
    </b-container>
</template>

<script>

import { Component } from 'vue-property-decorator'
import { stateApiService } from '@/ts/restApi'
import { GeneralMixin } from '@/mixins/general-mixin'

import {
  ServiceStateInfo,
  ServiceStateInfoModeEnum,
  ServiceStateInfoStateEnum
} from '@/service'

@Component({
  components: {}
})

export default class ServiceStatusCard extends GeneralMixin {
  state = ServiceStateInfoStateEnum.RED
  mode = ServiceStateInfoModeEnum.MANUAL

  getChevronRotate () {
    if (this.tlVisible) {
      return 180
    } else {
      return 0
    }
  }

  // Visibility of traffic light
  tlVisible = false

  states = [
    {
      color: 'RED',
      description: 'Alle workers er stoppet.',
      mode: 'AUTO',
      setState: 'MANUAL',
      setStateButtonLabel: 'START / ST I MANUAL'
    },
    {
      color: 'YELLOW',
      description: 'Restart workers krer/ mode normal.',
      setState: 'MANUAL',
      mode: 'NORMAL',
      setStateButtonLabel: undefined
    },
    {
      color: 'GREEN',
      description: 'Alle workers krer.', // Mode normal.
      mode: 'NORMAL',
      setState: 'MANUAL',
      setStateButtonLabel: 'STOP / ST I MANUAL'
    },
    {
      color: 'BLACK',
      description: 'Alle workers er stoppet.', // - Mode er manual.
      mode: 'MANUAL',
      setState: 'NORMAL',
      setStateButtonLabel: 'START / ST I AUTO'
    }
  ]

  getStateByColor (state) {
    const x = this.states.filter(f => {
      return f.color === state
    })
    return x[0]
  }

  toWorkers () {
    this.$router.push({ path: 'workers' })
  }

  toProperties () {
    this.$router.push({ path: 'properties' })
  }

  refresh () {
    this.getState()
    // console.log('ServiceStatusCard refreshed')
  }

  async getState () {
    try {
      const response = await stateApiService.getState()
      this.state = response.data.stateInfo.state
      this.mode = response.data.stateInfo.mode
    } catch (err) {
      console.log('aa')
    }
  }

  async setState (state) {
    console.log('state')
    try {
      const response = await stateApiService.setMode(state)
      this.getState()
    } catch (err) {
      console.log(err)
    }
  }

  isDisabled (state) {
    return this.state !== state
    // this.decreaseBarValue()
  }

  getStateImage (state) {
    return require('../assets/' + state + '.png')
  }

  getStatusImg () {
    return require('../assets/' + this.state + '.png')
  }

  async buttClick (action) {
    console.log(action)
    if (action === 'start') {
      try {
        const response = await stateApiService.setMode('NORMAL')
        console.log(response)
        try {
          this.getState()
        } catch {
          console.log('start ok')
        }
      } catch (err) {
        console.log(err)
      }
    }

    if (action === 'stop') {
      try {
        const response = await stateApiService.setMode('MANUAL')
        console.log(response)
        try {
          this.getState()
        } catch {
          console.log('start ok')
        }
      } catch (err) {
        console.log('buttClick() catch', err)
      }
      // this.state = ServiceStateInfoStateEnum.RED
    }
    if (action === 'resume') {
      this.state = ServiceStateInfoStateEnum.GREEN
    }
    if (action === 'suspend') {
      this.state = ServiceStateInfoStateEnum.YELLOW
    }
  }
}
</script>

