import { Component, Watch } from 'vue-property-decorator'
import { LedningsEjer } from '@/service'
import { configuationService } from '@/ts/restApi'
import { IntervalMixin } from '@/mixins/interval-mixin'

/**
 * Mixin for the CVR filter inserted into flere views.
 */
@Component
export class EjerMixin extends IntervalMixin {
  ejere: LedningsEjer[] = []
  cvr?: string = ''
  ejer?: string | (string | null)[] = ''

  async beforeMount (): Promise<void> {
    if (this.ejere.length === 0) {
      try {
        const response = configuationService.getEjere()
        response.then((x) => {
          this.ejere = x.data.ejerList || []
          if (this.$route.query.cvr) {
            this.ejer = this.getEjerLabelByCVR(x.data.ejerList || [])
          }
        })
      } catch (err) {
        console.log('Ejere failed', err)
      }
    }
  }

  /*
  Get a list of Ejere as a dict so the select understands it properly.
   */
  getEjere (): { label: string | undefined; code: number | undefined }[] {
    const ee: { label: string | undefined; code: number | undefined }[] = []
    this.ejere.forEach(e => {
      ee.push({ label: e.ejerCvr + ' : ' + e.ejerNavn || undefined, code: e.ejerCvr })
    })
    return ee
  }

  // getEjerByCVR (cvr: number): LedningsEjer {
  //   return this.ejere.filter(ejer => ejer.ejerCvr === cvr)
  // }

  getEjerLabelByCVR (ejere: LedningsEjer[]): string {
    let e
    console.log('FE', ejere)
    ejere.forEach(ejer => {
      console.log(ejer, ' >> ', this.$route.query.cvr, ejer.ejerCvr === Number(this.$route.query.cvr))
      if (ejer.ejerCvr === Number(this.$route.query.cvr)) {
        e = ejer.ejerCvr + ' : ' + ejer.ejerNavn
      }
    })
    return e || ''
  }

  /**
   * Adds and removes the CVR as a parameter to the url
   * @param newValue
   */
  @Watch('ejer')
  ejerChanged (newValue: { code: string | undefined }): void {
    console.log('mixins.ejerChanged()')
    if (newValue) {
      this.cvr = newValue.code
      if (this.cvr) {
        console.log('adding', this.cvr)
        this.addParamsToLocation({ cvr: String(this.cvr) })
        // this.load()
      }
    } else {
      this.cvr = undefined
      this.removeParamFromLocation('cvr')
    }
  }

  getChosenCvr (): string {
    return this.cvr || ''
  }
}
